import React from "react";
import AnchorLink from "react-anchor-link-smooth-scroll";
import ToggleTheme from "components/theme/Header/ToggleTheme";
import { Wrapper } from "./styles";
import { Link } from "gatsby";
// import {resume} from '../../../../assets/works/resume.pdf'

const NavbarLinks = ({ desktop }) => (
  <Wrapper desktop={desktop}>
    <AnchorLink href="#about">About</AnchorLink>
    <AnchorLink href="#works">Works</AnchorLink>
    <AnchorLink href="#medium">Blogs</AnchorLink>
    <AnchorLink href="#contact">Contact</AnchorLink>

    {/* <AnchorLink href="#showcase">Showcase</AnchorLink> */}
    {/* <Link to="/resume">Resume</Link> */}
    {/* <a href="../../../../assets/works/resume.pdf" target="__blank" download="resume.pdf">Resume</a> */}
    <a href="https://anyesh.netlify.com/" target="__blank">
      Resume
    </a>
    <ToggleTheme />
  </Wrapper>
);

export default NavbarLinks;
